import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select, message, Spin } from 'antd';
import { getTypeOfReward, updateTypeOfReward, createTypeOfReward } from 'services/typeOfReward';
import { getListBonusType } from 'services/BonusType';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    level: '',
    type: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

const { Option } = Select;

@Form.create({ name: 'formTypeOfRewards' })
class FormTypeOfRewardsPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      systemId: false,
      initialValue: {},
      loading: false,
      listSelect: [],
      valuebtId: '',
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    this.getListData();
    if (match.params.id) {
      this.setState({ systemId: match.params.id, loading: true });
      getTypeOfReward(match.params.id)
        .then(res => {
          const initialValue = handleInitialValue(res.data);
          const valuebtId = initialValue.btId;
          this.setState({ initialValue, loading: false, valuebtId });
        })
        .catch(error => {
          console.log('error', error);
          this.setState({ loading: false });
        });
    }
  };

  getListData = page => {
    getListBonusType(page, false)
      .then(res => {
        this.setState({
          listSelect: res.data.items,
        });
      })
      .catch(error => {
        console.log('====>', error);
      });
  };

  handleSubmit = e => {
    const { match } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (match.params.id) {
          updateTypeOfReward(match.params.id, values)
            .then(() => {
              message.success('Chỉnh sửa hệ đào tạo thành công');
              this.props.history.push('/typeOfRewards/list');
            })
            .catch(error => {
              console.log('res', error);
              // message.error(error.response.data.reason);
            });
        } else {
          createTypeOfReward(values)
            .then(() => {
              message.success('Thêm hệ đào tạo thành công');
              this.props.history.push('/typeOfRewards/list');
            })
            .catch(error => {
              console.log('res', error.response);
              // message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { initialValue, valuebtId } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId, listSelect, loading } = this.state;
    return (
      <Card title={systemId ? 'Cập nhật loại khen thưởng' : 'Thêm loại khen thưởng'}>
        <Spin spinning={loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item
                style={{ display: 'none' }}
                hasFeedback
                label="Cấp khen thưởng"
                {...formItemLayout}
              >
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                })(<Input />)}
              </Form.Item>
              <Form.Item hasFeedback label="Cấp khen thưởng" {...formItemLayout}>
                {getFieldDecorator('btId', {
                  initialValue: `${valuebtId}`,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(
                  <Select style={{ width: '100%' }} placeholder="Chọn cấp khen thưởng">
                    {listSelect.length > 0
                      ? listSelect.map(item => <Option key={item.id}>{item.label}</Option>)
                      : null}
                  </Select>
                )}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Loại khen thưởng" hasFeedback>
                {getFieldDecorator('code', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTypeOfRewardsPage {...props} />
  </Page>
));



